c;\n}\n\n.hljs-tag,\n.hljs-selector-tag,\n.hljs-regexp,\n.hljs-meta {\n    color: $magenta\n}\n\n.hljs-string,\n.hljs-subst {\n    color: $green;\n}\n\n.hljs-number,\n.hljs-variable,\n.hljs-template-variable {\n    color: $teal;\n}\n\n.hljs-name,\n.hljs-keyword,\n.hljs-type,\n.hljs-attribute {\n    color: shade($blue, 20%);\n}\n\n.hljs-title,\n.hljs-function > .hljs-title,\n.hljs-symbol,\n.hljs-bullet,\n.hljs-built_in,\n.hljs-builtin-name,\n.hljs-link {\n    color: $dark-blue;\n}\n\n.hljs-params {\n    color: $deep-red;\n}\n\n.hljs-addition {\n    color: $azure;\n    display: inline-block;\n    width: 100%;\n}\n\n.hljs-deletion {\n    color: $red-orange;\n    display: inline-block;\n    width: 100%;\n}\n\n.hljs-selector-id,\n.hljs-selector-class {\n    color: $slate-gray-400;\n}\n\n.hljs-emphasis {\n    font-style: italic;\n}\n\n.hljs-strong {\n    font-weight: bold;\n}\n\n.hljs-link {\n    text-decoration: underline;\n}\n","@import './variables';\n\n/// Slightly lighten a color\n/// @param {Color} $color - color to tint\n/// @param {Number} $percentage - percentage of `$color` in returned color\n/// @return {Color}\n@function tint($color, $percentage) {\n    @return mix(white, $color, $percentage);\n}\n\n/// Slightly darken a color\n/// @param {Color} $color - color to shade\n/// @param {Number} $percentage - percentage of `$color` in returned color\n/// @return {Color}\n@function shade($color, $percentage) {\n    @return mix(black, $color, $percentage);\n}\n\n/// Returns the rem value based on the px value that has been passed in\n/// @param {Size} $size - pixel based size to convert to rem\n@function calculateRem($size) {\n    $remSize: $size / $default-font-size;\n    @return #{$remSize}rem;\n}\n",".hc-divider {\n    height: 1px;\n    width: 100%;\n    margin: 20px 0;\n    border-top: 1px solid $gray-200;\n}\n","// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    // Note: it's important for this one to start off `absolute`,\n    // in order for us to be able to measure it correctly.\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n\n    // For connected-position overlays, we set `display: flex` in\n    // order to force `max-width` and `max-height` to take effect.\n    display: flex;\n    max-width: 100%;\n    max-height: 100%;\n  }\n\n  .cdk-overlay-backdrop {\n    // TODO(jelbourn): reuse sidenav fullscreen mixin.\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 1;\n\n      // In high contrast mode the rgba background will become solid so we need to fall back\n      // to making it opaque using `opacity`. Note that we can't use the `cdk-high-contrast`\n      // mixin, because we can't normalize the import path to the _a11y.scss both for the\n      // source and when this file is distributed. See #10908.\n      @media screen and (-ms-high-contrast: active) {\n        opacity: 0.6;\n      }\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from\n    // capturing the user's mouse scroll events. Since we also can't use something like\n    // `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at\n    // all and using `opacity` to make the element transparent.\n    &, &.cdk-overlay-backdrop-showing {\n      opacity: 0;\n    }\n  }\n\n  // Overlay parent element used with the connected position strategy. Used to constrain the\n  // overlay element's size to fit within the viewport.\n  .cdk-overlay-connected-position-bounding-box {\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n\n    // We use `display: flex` on this element exclusively for centering connected overlays.\n    // When *not* centering, a top/left/bottom/right will be set which overrides the normal\n    // flex layout.\n    display: flex;\n\n    // We use the `column` direction here to avoid some flexbox issues in Edge\n    // when using the \"grow after open\" options.\n    flex-direction: column;\n\n    // Add some dimensions so the element has an `innerText` which some people depend on in tests.\n    min-width: 1px;\n    min-height: 1px;\n  }\n\n  // Used when disabling global scrolling.\n  .cdk-global-scrollblock {\n    position: fixed;\n\n    // Necessary for the content not to lose its width. Note that we're using 100%, instead of\n    // 100vw, because 100vw includes the width plus the scrollbar, whereas 100% is the width\n    // that the element had before we made it `fixed`.\n    width: 100%;\n\n    // Note: this will always add a scrollbar to whatever element it is on, which can\n    // potentially result in double scrollbars. It shouldn't be an issue, because we won't\n    // block scrolling on a page that doesn't have a scrollbar in the first place.\n    overflow-y: scroll;\n  }\n}\n","@import '~@angular/cdk/_overlay.scss';\n@import './variables';\n\n// override index for implementation inside a modal: https://github.com/HealthCatalyst/Fabric.Cashmere/issues/867\n$cdk-z-index-overlay-container: $zindex-cdk-overlay;\n$cdk-z-index-overlay: $zindex-cdk-overlay;\n\n@include cdk-overlay();\n\n.hc-menu-panel {\n    position: relative;\n    font-family: $default-font-family;\n    display: flex;\n    flex-direction: column;\n    padding: 5px 16px;\n    margin: -12px;\n    background-color: $white;\n    border-radius: 4px;\n\n    .hc-divider {\n        width: auto;\n        margin: 5px 0;\n    }\n}\n\n.hc-divider.hc-menu-item {\n    margin: 5px -16px;\n}\n\nbutton.hc-menu-item,\na.hc-menu-item {\n    @include fontSize(14px);\n    font-family: $default-font-family;\n    color: $offblack;\n    padding: 6px 16px;\n    margin: 0 -16px;\n    border: 0;\n    background: 0 0;\n    text-align: left;\n    display: flex;\n    align-items: center;\n\n    &[disabled] {\n        opacity: 0.5;\n    }\n\n    &:active,\n    &:hover {\n        color: $offblack;\n    }\n\n    &:focus:not([disabled]) {\n        background-color: tint($blue, 70%);\n        color: $offblack;\n        cursor: pointer;\n        box-shadow: none;\n        outline: none;\n    }\n\n    &.hc-menu-item-submenu {\n        padding-right: 40px;\n    }\n\n    &.hc-menu-item-submenu:after {\n        content: '';\n        background-image: url('data:image/svg+xml;base64,');\n        position: absolute;\n        width: 20px;\n        height: 20px;\n        right: 10px;\n    }\n\n    .hc-menu-icon {\n        font-size: 16px;\n        height: 16px;\n        width: 16px;\n        margin-right: 14px;\n    }\n\n    .hc-menu-sub-text {\n        flex: 1 0 auto;\n        margin-left: 15px;\n        color: $gray-300;\n        text-align: right;\n\n        hc-icon {\n            margin-right: 0;\n            color: $offblack;\n        }\n    }\n}\n","// Cashmere theming for the third-party ng-select component.\n// https://github.com/ng-select/ng-select\n\n@import './colors';\n@import './mixins';\n@import './select';\n@import './chip';\n@import './variables';\n\n$ng-select-primary-text: $offblack !default;\n$ng-select-disabled-text: $gray-200 !default;\n$ng-select-border: $slate-gray-300 !default;\n$ng-select-border-focus: $primary-brand !default;\n$ng-select-border-radius: 0 !default;\n$ng-select-border-width: 1px !default;\n$ng-select-bg: $white !default;\n$ng-select-option-selected: $azure !default;\n$ng-select-option-hover: tint($primary-brand, 75%) !default;\n$ng-select-placeholder: lighten($ng-select-primary-text, 40) !default;\n$ng-select-height: 35px !default;\n$ng-select-value-padding-left: 9px !default;\n$ng-select-value-font-size: 14px !default;\n\n.ng-select {\n    &.ng-select-opened {\n        > .ng-select-container, >.ng-select-container:hover {\n            border-color: $ng-select-border-focus;\n            background-color: $white;\n            box-shadow: 0 0 3px $primary-brand;\n        }\n        &.ng-select-bottom {\n            > .ng-select-container {\n                border-bottom-right-radius: 0;\n                border-bottom-left-radius: 0;\n            }\n        }\n        &.ng-select-top {\n            > .ng-select-container {\n                border-top-right-radius: 0;\n                border-top-left-radius: 0;\n            }\n        }\n    }\n    &.ng-select-focused {\n        &:not(.ng-select-opened) > .ng-select-container {\n            border-color: $ng-select-border-focus;\n            background-color: $white;\n            box-shadow: 0 0 3px $primary-brand;\n        }\n    }\n    &.ng-select-disabled {\n        > .ng-select-container {\n            background-color: $ng-select-disabled-text;\n        }\n    }\n    .ng-has-value .ng-placeholder {\n        display: none;\n    }\n    .ng-select-container {\n        background-color: $ng-select-bg;\n        border-radius: $ng-select-border-radius;\n        border: $ng-select-border-width solid $ng-select-border;\n        min-height: $ng-select-height;\n        align-items: center;\n\n        &:hover {\n            border-color: $ng-select-border-focus;\n            background-color: tint($ng-select-border-focus, 95%);\n        }\n\n        .ng-value-container {\n            line-height: 1.3;\n            align-items: center;\n            padding-left: $ng-select-value-padding-left;\n            .ng-placeholder {\n                color: $ng-select-placeholder;\n            }\n            input { @include fontSize($ng-select-value-font-size)}\n        }\n    }\n    &.ng-select-single {\n        .ng-select-container {\n            height: $ng-select-height;\n            .ng-value-container {\n                .ng-input {\n                    top: 5px;\n                    left: 0;\n                    padding-left: $ng-select-value-padding-left;\n                    padding-right: 50px;\n                }\n            }\n        }\n    }\n    &.ng-select-multiple {\n        &.ng-select-disabled {\n            > .ng-select-container .ng-value-container .ng-value {\n                background-color: $ng-select-disabled-text;\n                border: 1px solid lighten($ng-select-border, 10);\n                .ng-value-label {\n                    padding: 0 5px;\n                }\n            }\n        }\n        .ng-select-container {\n            .ng-value-container {\n                padding-left: 7px;\n\n                /** .ng-value is the library's class name for the selected \"chips\" */\n                .ng-value {\n                    @include hc-chip();\n                    @include hc-chip-neutral();\n                    @include hc-chip-padding();\n                    margin: 3px;\n                    position: relative;\n                    &.ng-value-disabled {\n                        background-color: $ng-select-disabled-text;\n                        .ng-value-label {\n                            padding-left: 5px;\n                        }\n                    }\n                    .ng-value-label {\n                        max-width: 200px;\n                        padding: 3px 0;\n                        overflow: hidden;\n                        text-overflow: ellipsis;\n                        white-space: nowrap;\n                    }\n                    .ng-value-icon {\n                        color: transparent;\n                        position: relative;\n                        top: 7px;\n                        left: -7px;\n                        @include hc-chip-close-icon();\n                        @include hc-chip-close-icon-neutral();\n                        opacity: 0.3;\n                        &:hover {\n                            opacity: 0.6;\n                        }\n\n                        &.right {\n                            left: 7px;\n                        }\n                    }\n                }\n                .ng-input {\n                    padding: 0 0 0 3px;\n                }\n                .ng-placeholder {\n                    top: 7px;\n                    padding-left: 3px;\n                }\n            }\n        }\n    }\n\n    /** Button next to the dropdown arrow to clear out the input */\n    .ng-clear-wrapper {\n        .ng-clear {color: transparent; }\n        &:after {\n            content: '';\n            display: block;\n            opacity: 0.5;\n            background-image: url('data:image/png;base64,');\n            background-repeat: no-repeat;\n            background-size: contain;\n            box-sizing: border-box;\n            height: 24px;\n            width: 24px;\n            position: absolute;\n            left: 0;\n            top: 50%;\n            margin-top: -12px;\n        }\n        &:hover:after {\n            opacity: 0.8;\n        }\n    }\n    .ng-spinner-zone {\n        padding: 5px 5px 0 0;\n    }\n    .ng-arrow-wrapper {\n        @include hc-select-chevron();\n        pointer-events: auto;\n        &:after {\n            display: block;\n            pointer-events: auto;\n        }\n    }\n\n    .ng-select-container .ng-arrow-wrapper {\n        height: 33px;\n    }\n}\n\nng-dropdown-panel.ng-dropdown-panel {\n    background-color: $ng-select-bg;\n    border: 1px solid $ng-select-border;\n    box-shadow: 0 1px 0 $shadow;\n    left: 0;\n    z-index: $z-index-ng-select-dropdown;\n    &.ng-select-bottom {\n        top: 100%;\n        border-bottom-right-radius: 4px;\n        border-bottom-left-radius: 4px;\n        border-top-color: lighten($ng-select-border, 10);\n        margin-top: 0;\n        .ng-dropdown-panel-items {\n            .ng-option {\n                &:last-child {\n                    border-bottom-right-radius: 4px;\n                    border-bottom-left-radius: 4px;\n                }\n            }\n        }\n    }\n    &.ng-select-top {\n        bottom: 100%;\n        border-top-right-radius: 4px;\n        border-top-left-radi