./mixins';\n\n/* Datepicker */\n\n@mixin hc-datepicker-content() {\n    background-color: $white;\n    border-radius: 4px;\n    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);\n    color: $text;\n    display: block;\n    height: 100%;\n    width: 310px;\n}\n\n@mixin hc-datepicker-toggle-active() {\n    color: $primary-brand;\n}\n\n/* Calendar */\n\n$hc-datepicker-calendar-padding: 15px;\n$hc-datepicker-non-touch-calendar-cell-size: 40px;\n$hc-datepicker-non-touch-calendar-width: $hc-datepicker-non-touch-calendar-cell-size * 7 + $hc-datepicker-calendar-padding * 2;\n\n$hc-calendar-padding: 8px !default;\n$hc-calendar-header-divider-width: 2px !default;\n$hc-calendar-controls-vertical-padding: 5%;\n// We use the same padding as the month / year label, but subtract 16px since there is padding\n// between the edge of the button and the text. This ensures that the button text lines up with\n// the month / year label text.\n$hc-calendar-controls-side-margin: calc(33% / 7 - 16px);\n\n$hc-calendar-arrow-size: 5px !default;\n\n// Values chosen to approximate https://material.io/icons/#ic_navigate_before and\n// https://material.io/icons/#ic_navigate_next as closely as possible.\n$hc-calendar-prev-next-icon-border-width: 2px;\n$hc-calendar-prev-next-icon-margin: 15.5px;\n$hc-calendar-prev-icon-transform: translateX(2px) rotate(-45deg);\n$hc-calendar-next-icon-transform: translateX(-2px) rotate(45deg);\n\n@mixin hc-calendar() {\n    display: block;\n    height: 100%;\n    padding: 10px 0 20px 0;\n    width: $hc-datepicker-non-touch-calendar-width;\n}\n\n@mixin hc-calendar-header() {\n    padding: 0 $hc-calendar-padding;\n}\n\n@mixin hc-calendar-content() {\n    outline: none;\n    padding: 0 $hc-calendar-padding $hc-calendar-padding $hc-calendar-padding;\n}\n\n@mixin hc-calendar-controls() {\n    display: flex;\n    justify-content: space-between;\n    margin: 0 $hc-calendar-controls-side-margin $hc-calendar-controls-vertical-padding;\n}\n\n@mixin hc-calendar-control-button() {\n    font-size: $default-font-size;\n    min-width: inherit;\n    padding: 10px;\n}\n\n@mixin hc-calendar-dropdown-caret() {\n    border-left: $hc-calendar-arrow-size solid transparent;\n    border-right: $hc-calendar-arrow-size solid transparent;\n    border-top-style: solid;\n    border-top-width: $hc-calendar-arrow-size;\n    display: inline-block;\n    height: 0;\n    margin: 0 0 0 $hc-calendar-arrow-size;\n    vertical-align: middle;\n    width: 0;\n\n    [dir='rtl'] & {\n        margin: 0 $hc-calendar-arrow-size 0 0;\n    }\n}\n\n@mixin hc-calendar-dropdown-caret-invert() {\n    transform: rotate(180deg);\n}\n\n@mixin hc-calendar-control-arrow() {\n    color: $primary-brand;\n    height: 40px;\n    min-width: inherit;\n    padding: 0px;\n    position: relative;\n    top: -2px;\n    width: 40px;\n\n    &::after {\n        border: 0 solid currentColor;\n        border-top-width: $hc-calendar-prev-next-icon-border-width;\n        bottom: 0;\n        content: '';\n        left: 0;\n        margin: $hc-calendar-prev-next-icon-margin;\n        position: absolute;\n        right: 0;\n        top: 0;\n    }\n\n    [dir='rtl'] & {\n        transform: rotate(180deg);\n    }\n}\n\n@mixin hc-calendar-previous-button() {\n    &::after {\n        border-left-width: $hc-calendar-prev-next-icon-border-width !important;\n        transform: $hc-calendar-prev-icon-transform;\n    }\n}\n\n@mixin hc-calendar-next-button() {\n    &::after {\n        border-right-width: $hc-calendar-prev-next-icon-border-width !important;\n        transform: $hc-calendar-next-icon-transform;\n    }\n}\n\n@mixin hc-calendar-month-label() {\n    align-items: center;\n    color: $primary-brand;\n    display: inline-flex;\n    font-size: calculateRem(15px);\n    font-weight: 600;\n    height: 35px;\n    justify-content: center;\n    line-height: 15px;\n    min-width: 35px;\n    vertical-align: baseline;\n}\n\n@mixin hc-calendar-table() {\n    border-collapse: collapse;\n    border-spacing: 0;\n    width: 100%;\n}\n\n@mixin hc-calendar-table-header() {\n    color: $primary-brand;\n}\n\n@mixin hc-calendar-table-header-cell() {\n    @include fontSize(9px);\n    color: $gray-500;\n    padding: 0 0 $hc-calendar-padding 0;\n    text-align: center;\n}\n\n@mixin hc-calendar-table-header-divider() {\n    height: $hc-calendar-header-divider-width;\n    position: relative;\n\n    // We use an absolutely positioned pseudo-element as the divider line for the table header so we\n    // can extend it all the way to the edge of the calendar.\n    &::after {\n        background: $gray-200;\n        content: '';\n        height: $hc-calendar-header-divider-width;\n        left: 0;\n        position: absolute;\n        right: 0;\n        top: 0;\n    }\n}\n\n@mixin hc-calendar-time-picker() {\n    align-items: center;\n    display: flex;\n    justify-content: center;\n    margin-top: 10px;\n}\n\n@mixin hc-calendar-time-picker-field() {\n    margin: 0 3px;\n}\n\n@mixin hc-calendar-time-picker-input() {\n    width: 25px;\n}\n\n@mixin hc-calendar-time-controls() {\n    display: flex;\n    flex-direction: column;\n}\n\n@mixin hc-calendar-time-controls-button() {\n    /* important is needed here to override hc-button properties */\n    height: 16px !important;\n    line-height: 16px !important;\n    padding: 0 !important;\n    width: 16px !important;\n}\n\n@mixin hc-calendar-controls-icon() {\n    line-height: 1 !important;\n}\n\n@mixin hc-calendar-time-picker-select() {\n    width: 70px;\n}\n\n@mixin hc-calendar-time-picker-icon() {\n    color: $slate-gray-400;\n    font-size: 25px;\n    height: 25px;\n    margin-right: 10px;\n    width: 25px;\n}\n\n/* Datepicker Toggle */\n\n@mixin hc-calendar-toggle() {\n    background: 0 0;\n    border: none;\n    box-sizing: border-box;\n    color: $primary-brand;\n    cursor: pointer;\n    display: inline-block;\n    height: 32px;\n    line-height: 1.5;\n    margin: 0;\n    outline: 0;\n    overflow: visible;\n    padding: 0;\n    position: relative;\n    text-align: center;\n    text-decoration: none;\n    user-select: none;\n    vertical-align: baseline;\n    -webkit-tap-highlight-color: transparent;\n    white-space: nowrap;\n    width: 32px;\n}\n\n/* Calendar Body */\n\n$hc-calendar-body-label-padding-start: 5% !default;\n// We don't want the label to jump around when we switch between month and year views, so we use\n// the same amount of padding regardless of the number of columns. We align the header label with\n// the one third mark of the first cell, this was chosen somewhat arbitrarily to make it look\n// roughly like the mock. Half way is too far since the cell text is center aligned.\n$hc-calendar-body-label-side-padding: 33% / 7 !default;\n$hc-calendar-body-cell-min-size: 32px !default;\n$hc-calendar-body-cell-content-margin: 5% !default;\n$hc-calendar-body-cell-content-border-width: 1px !default;\n\n$hc-calendar-body-min-size: 7 * $hc-calendar-body-cell-min-size !default;\n$hc-calendar-body-cell-content-size: 100% - $hc-calendar-body-cell-content-margin * 2 !default;\n$hc-calendar-body-disabled-color: $slate-gray-300 !default;\n$hc-datepicker-selected-today-box-shadow-width: 1px;\n\n@mixin hc-calendar-body() {\n    font-size: $default-font-size;\n    min-width: $hc-calendar-body-min-size;\n}\n\n@mixin hc-calendar-body-label() {\n    color: $primary-brand;\n    font-size: $default-font-size;\n    height: 0;\n    line-height: 0;\n    padding-left: $hc-calendar-body-label-side-padding;\n    padding-right: $hc-calendar-body-label-side-padding;\n    text-align: left;\n\n    [dir='rtl'] & {\n        text-align: right;\n    }\n}\n\n@mixin hc-calendar-body-cell() {\n    cursor: pointer;\n    height: 0;\n    line-height: 0;\n    outline: none;\n    position: relative;\n    text-align: center;\n}\n\n@mixin hc-calendar-body-disabled() {\n    cursor: default;\n}\n\n@mixin hc-calendar-body-cell-content() {\n    align-items: center;\n    border-color: transparent;\n    // Choosing a value clearly larger than the height ensures we get the correct capsule shape.\n    border-radius: 999px;\n    border-style: solid;\n    border-width: $hc-calendar-body-cell-content-border-width;\n    box-sizing: border-box;\n    color: $text;\n    display: flex;\n    height: $hc-calendar-body-cell-content-size;\n    justify-content: center;\n    left: $hc-calendar-body-cell-content-margin;\n    // Prevents text being off-center on Android.\n    line-height: 1;\n    position: absolute;\n    top: $hc-calendar-body-cell-content-margin;\n    width: $hc-calendar-body-cell-content-size;\n}\n\n@mixin hc-calendar-body-selected() {\n    background-color: darken($primary-brand, 10%);\n    color: $white;\n    font-weight: 700;\n}\n\n@mixin hc-calendar-body-selected-disabled() {\n    background-color: $hc-calendar-body-disabled-color;\n}\n\n@mixin hc-calendar-body-selected-today() {\n    box-shadow: inset 0 0 0 $hc-datepicker-selected-today-box-shadow-width $hc-calendar-body-disabled-color;\n}\n\n@mixin hc-calendar-body-disabled() {\n    color: $hc-calendar-body-disabled-color;\n}\n\n@mixin hc-calendar-body-active() {\n    border: 2px solid darken($primary-brand, 10%);\n}\n\n@mixin hc-calendar-body-today() {\n    background-color: $hc-calendar-body-disabled-color;\n    // Note: though it's not text, the border is a hint about the fact that this is today's date,\n    // so we use the hint color.\n    border-color: transparent;\n}\n\n@mixin hc-calendar-body-today-disabled() {\n    background-color: $hc-calendar-body-disabled-color;\n    border-color: transparent;\n}\n","@import './colors';\n@import './variables';\n@import './mixins';\n\n.error-tile {\n    width: 100%;\n    max-width: 660px;\n    margin: 0 auto;\n    box-sizing: border-box;\n    background-color: $white;\n    border-radius: 5px;\n    display: block;\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);\n\n    .content-column {\n        width: 48%;\n        padding: 50px 0;\n        display: inline-block;\n        text-align: center;\n        vertical-align: middle;\n    }\n\n    .error-header-code {\n        font-size: 9rem;\n        font-weight: 800;\n        color: $primary-brand;\n        text-align: left;\n    }\n\n    .error-header-string {\n        font-size: 3rem;\n        font-weight: 800;\n        color: $primary-brand;\n        text-align: left;\n        margin: 10px 0;\n    }\n\n    .error-img {\n        max-width: 220px;\n        height: 171px;\n        display: block;\n        width: 70%;\n        margin: 10px auto 0 auto;\n        background-repeat: no-repeat;\n        background-position: center center;\n        background-image: url('data:image/svg+xml;base64,');\n    }\n\n    .access-error-img {\n        max-width: 220px;\n        height: 171px;\n        display: block;\n        width: 70%;\n        margin: 10px auto 0 auto;\n        background-repeat: no-repeat;\n        background-position: center center;\n        background-image: url('data:image/svg+xml;base64,');\n    }\n\n    .error-message {\n        @include fontSize(20px);\n        font-weight: 600;\n        color: $primary-brand;\n        line-height: 1.2;\n        text-align: left;\n    }\n\n    .error-message-light {\n        @include fontSize(19px);\n        font-weight: 500;\n        color: $primary-brand;\n        line-height: 1.3;\n        text-align: left;\n    }\n\n    hr {\n        margin: 0;\n    }\n\n    .error-button {\n        display: block;\n        margin: 20px auto;\n        padding: 0 20px;\n        height: 35px;\n        min-width: 215px;\n        font-size: 15px;\n        line-height: 35px;\n        font-weight: normal;\n        text-align: center;\n        white-space: nowrap;\n        touch-action: manipulation;\n        cursor: pointer;\n        border-radius: 5px;\n        font-family: $default-font-family;\n        background-color: $green;\n        color: $white;\n\n        &:hover {\n            background-color: shade($green, 10%);\n        }\n    }\n\n    @include media-breakpoint-down(sm) {\n        .content-column {\n            width: 100%;\n            padding: 0;\n        }\n        .error-img {\n            display: none;\n        }\n        .error-header-string {\n            text-align: center;\n            padding-top: 20px;\n        }\n        .error-header-code {\n            text-align: center;\n            padding-top: 20px;\n        }\n        .error-message {\n            text-align: center;\n            padding: 0 30px 30px 30px;\n        }\n        .error-message-light {\n            text-align: center;\n            padding: 0 30px 30px 30px;\n        }\n    }\n}\n","@import './colors';\n@import './mixins';\n@import './variables';\n\n.hc-login-container {\n    // font\n    font-size: 16px;\n    font-family: $default-font-family;\n    font-size: 16px;\n\n    // background\n    // background-image: url(../assets/login_bg.jpg);\n\n    background-repeat: no-repeat;\n    background-position: center top;\n    background-size: cover;\n\n    @include media-breakpoint-down(sm) {\n        background-image: none !important;\n    }\n    //\n\n    // position\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    height: 100%;\n    width: 100%;\n}\n\n.hc-login {\n    @include media-breakpoint-up(sm) {\n        width: 400px;\n        padding: 30px 40px;\n        box-shadow: 0 0 13px rgba($black, 0.25);\n        border-radius: 5px;\n        margin-bottom: 50px;\n        background: $slate-gray-100;\n    }\n\n    fieldset {\n        border: 0;\n    }\n\n    label {\n        font-size: 1rem;\n        color: $slate-gray-500;\n        font-weight: normal;\n        margin-top: 20px;\n    }\n\n    .login-input {\n        width: 100%;\n        height: 40px;\n        padding: 0px 10px;\n        margin-bottom: 20px;\n        margin-top: 5px;\n        box-sizing: border-box;\n        border-color: $slate-gray-200;\n        border-image: none;\n        border-style: solid;\n        font-family: inherit;\n        font-size: inherit;\n        line-height: inherit;\n    }\n\n    .hc-form-field {\n        display: block;\n    }\n\n    a {\n        color: $blue;\n        text-decoration: none;\n    }\n\n    .privacy {\n        line-height: 1rem;\n        color: $slate-gray-400;\n        padding: 0 30px;\n        text-align: center;\n        font-size: 12px;\n    }\n\n    .new-to-catalyst {\n        position: relative;\n        overflow: hidden;\n        text-align: center;\n        color: $slate-gray-400;\n        margin: 40px 0 20px;\n        > span {\n            padding: 5px;\n        }\n        &:before,\n        &:after {\n            position: absolute;\n            top: 51%;\n            overflow: hidden;\n            width: 50%;\n            height: 2px;\n            content: '\\a0';\n            background-color: $slate-gray-300;\n        }\n        &:before {\n            margin-left: -50%;\n            text-align: right;\n        }\n    }\n\n    .forgot-password {\n        float: right;\n        font-size: 0.75rem;\n        margin-top: 5px;\n    }\n\n    .logo {\n        max-width: 280px;\n        margin-left: auto;\n        margin-right: auto;\n        width: 100%;\n        margin-bottom: 30px;\n        padding-top: 10px;\n    }\n\n    .new-account {\n        text-align: center;\n        display: block;\n        font-weight: 600;\n    }\n\n    .login-button {\n        width: 100%;\n        height: 40px;\n        font-family: inherit;\n        font-size: inherit;\n        line-height: inherit;\n        background-color: $primary-action;\n        font-weight: 600;\n        color: $white;\n        border-radius: 10px;\n        border: none;\n        margin-top: 0.75rem;\n        margin-bottom: 10px;\n        &:hover {\n            background-color: shade($primary-action, 10%);\n        }\n        &:active {\n            background-color: shade($primary-action, 20%);\n        }\n    }\n}\n\n.hc-login-footer {\n    @include media-breakpoint-down(sm) {\n        display: none;\n    }\n    & > ul {\n        margin: 0;\n        list-style-type: none;\n        -webkit-padding-start: 0;\n        & > li {\n            display: inline;\n        }\n        & > li:not(:last-child):after {\n            content: '|';\n            color: $white;\n        }\n    }\n\n    a {\n        padding: 15px;\n        color: $white;\n        text-decoration: none;\n        font-size: 0.75rem;\n    }\n}\n","@import './modal';\n\n.hc-modal-header {\n    @include hc-modal-header();\n}\n\n.hc-modal-drag-handle {\n    @include hc-modal-drag-handle();\n}\n\n.hc-modal-body {\n    @include hc-modal-body();\n}\n\n.hc-modal-close-button {\n    @include hc-modal-close-button();\n}\n\n.hc-modal-footer {\n    @include hc-modal-footer();\n\n    .hc-button {\n        @include hc-modal-button-footer();\n    }\n}\n\n.hc-modal-content {\n    @include hc-modal-content();\n}\n\n.hc-modal-open {\n    @include hc-modal-open();\n}\n\n.hc-modal-center-component {\n    @include hc-modal-center-component();\n}\n","@import './colors';\n@import './functions';\n@import './mixins';\n\n$modal-padding: 15px 25px;\n$header-footer-height: 55px;\n$stacked-footer-height: 101px;\n$modal-border: 1px solid $gray-200;\n\n@mixin hc-modal-outer() {\n    align-items: center;\n    bottom: 0;\n    display: flex;\n    justify-content: center;\n    left: 0;\n    position: fixed;\n    right: 0;\n    top: 0;\n}\n\n@mixin hc-modal() {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    position: relative;\n}\n\n@mixin hc-modal-responsive() {\n    @include media-breakpoint-down(md) {\n        max-height: 100%;\n        height: 100%;\n    }\n\n    &.hc-modal-auto {\n        @include media-breakpoint-down(md) {\n            width: 100%;\n            justify-content: flex-end;\n        }\n    }\n\n    .hc-modal-body {        \n        @include media-breakpoint-down(md) {\n            margin-bottom: $stacked-footer-height;\n        }\n    }\n\n    .hc-modal-footer {\n        @include media-breakpoint-down(md) {\n            position: fixed;\n            flex-direction: column-reverse;\n        }\n        \n        .hc-button {\n            @include media-breakpoint-down(md) {\n                margin-left: 0;\n                width: 100%;\n            }\n        }\n    }\n    \n    .hc-modal-content {\n        @include media-breakpoint-down(md) {\n            height: calc(100% - 50px);\n            border-radius: 0;\n        }\n    }\n\n    .hc-modal-center-component {       \n        @include media-breakpoint-down(md) {\n            justify-content: flex-end;\n        }\n    }\n}\n\n@mixin hc-modal-auto() {\n    width: auto;\n    justify-content: center;\n}\n\n@mixin hc-modal-sm() {\n    width: 300px;\n    height: auto;\n    border-radius: 6px;\n\n    & .hc-modal-footer {\n        justify-content: flex-end;\n        flex-direction: row;\n        position: relative;\n    }\n    & .hc-modal-header {\n        justify-content: flex-start;\n    }\n    & .hc-modal-content {\n        border-radius: 6px;\n    }\n    & .hc-modal-body {\n        margin-bottom: 0;\n    }\n}\n\n@mixin hc-modal-md() {\n    width: 500px;\n}\n\n@mixin hc-modal-lg() {\n    width: 800px;\n}\n\n@mixin hc-modal-xl() {\n    width: 992px;\n}\n\n@mixin hc-modal-drag-handle() {\n    position: absolute;\n    right: 0;\n    top: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    color: $gray-300;\n    cursor: move;\n    width: 74px;\n    height: 54px;\n\n    // Drag handle is hidden when the modal switches to full-screen on smaller breakpoints\n    @include media-breakpoint-down(md) {\n        display: none;\n    }\n}\n\n@mixin hc-modal-header() {\n    align-items: center;\n    border-bottom: $modal-border;\n    color: $text;\n    display: flex;\n    font-size: calculateRem(18px);\n    font-weight: 600;\n    min-height: $header-footer-height;\n    padding: $modal-padding;\n    justify-content: flex-start;\n}\n\n@mixin hc-modal-body() {\n    overflow: auto;\n    padding: $modal-padding;\n    justify-content: space-between;\n    margin-bottom: 0;\n}\n\n@mixin hc-modal-close-button() {\n    background-color: transparent;\n    border: 0;\n    color: $text;\n    cursor: pointer;\n    font-size: 2rem;\n    font-weight: bold;\n    line-height: 1;\n    opacity: 0.5;\n    padding: 0;\n    text-shadow: 0 1px 0 $white;\n    -webkit-appearance: none;\n\n    &:focus,\n    &:hover {\n        color: $text;\n        opacity: 0.75;\n        text-decoration: none;\n    }\n}\n\n@mixin hc-modal-footer() {\n    align-items: center;\n    bottom: 0;\n    display: flex;\n    width: 100%;\n    border-top: $modal-border;\n    min-height: $header-footer-height;\n    padding: $modal-padding;\n    justify-content: flex-end;\n    flex-direction: row;\n    position: relative;\n}\n\n@mixin hc-modal-button-footer() {\n    margin-left: 10px;\n    width: auto;\n}\n\n@mixin hc-modal-content() {\n    background-color: $white;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    max-height: 100%;\n    border-radius: 6px;\n    height: auto;\n}\n\n@mixin hc-modal-open() {\n    overflow: hidden;\n}\n\n@mixin hc-modal-center-component() {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    justify-content: center;\n\n}\n","@import \"./colors\";\n@import \"./variables\";\n@import \"./mixins\";\n\n$navbar-color: $charcoal-blue !default;\n$navbar-brand: $primary-brand !default;\n$navbar-height: 53px !default;\n$navbar-app-padding-left: 25px !default;\n$navbar-app-padding-right: 50px !default;\n$navbar-text: $white !default;\n$navbar-text-inactive: $gray-300 !default;\n$navbar-fixed-shadow: 0px 2px 6px $shadow;\n\n@mixin hc-navbar-fixed-top {\n    top: 0;\n    position: fixed;\n    right: 0;\n    left: 0;\n    z-index: $zindex-navbar;\n    box-shadow: $navbar-fixed-shadow;\n}\n\n@mixin hc-navbar {\n    height: $navbar-height;\n    background